import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { Material } from './material';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class MaterialService {

  private urlAffiche:string="http://localhost:3000/rfq/rfqid/";
  private urlAdd:string="http://localhost:3000/rfq/";
  private urlEdit:string="http://localhost:3000/apiupdateMaterial";

  materials:Subject<any>=new Subject<Material[]>(); 
  materials$=this.materials.asObservable();
  constructor(private http:HttpClient) { }

  getMaterials(){
  this.http.get<any>(this.urlAffiche).subscribe((r)=>{
      let data=r.data; 
      this.materials.next(data);
    });
    return this.materials$;
  }
  getMaterial(sudyid:String){
    return this.http.get<any>(this.urlAffiche+sudyid+'/materials');
  }
  saveMaterial(idstudy:String,todo:any){
    return this.http.post<any>(this.urlAdd+idstudy+'/materials',todo);

  }
  EditMaterial(material:Material){
    return this.http.post<any>(this.urlEdit,material);

  }
  deleteMaterial(todo:Material){
    return this.http.delete(this.urlAffiche+'/'+todo.id);

  }
}
